"use client";
import React from "react";
import { useTranslation } from "react-i18next";

const Marquee = () => {
  const { t } = useTranslation();

  const handleClick = () => {
    const targetElement = document.getElementById("real-block");
    if (targetElement) {
      const headerOffset = 50; 
      const elementPosition = targetElement.getBoundingClientRect().top + window.pageYOffset;
      const offsetPosition = elementPosition - headerOffset;

      window.scrollTo({
        top: offsetPosition,
        behavior: "smooth",
      });
    }
  };

  return (
    <div className="wrap-line-text">
      <div className="marquee-container">
        <div className="marquee">
          <span onClick={handleClick}>
            {t("* Explore the Hottest New Brands ‚Äì")} <b>{t("Click Here!")}</b>{" "}
            {t("* Explore the Hottest New Brands ‚Äì")} <b>{t("Click Here!")}</b>{" "}
            {t("* Explore the Hottest New Brands ‚Äì")} <b>{t("Click Here!")}</b>{" "}
            {t("* Explore the Hottest New Brands ‚Äì")} <b>{t("Click Here!")}</b>{" "}
          </span>
          {/* –î—É–±–ª–∏—Ä—É–µ–º –∫–æ–Ω—Ç–µ–Ω—Ç –¥–ª—è –Ω–µ–ø—Ä–µ—Ä—ã–≤–Ω–æ–≥–æ —ç—Ñ—Ñ–µ–∫—Ç–∞ –ø—Ä–æ–∫—Ä—É—Ç–∫–∏ */}
          <span onClick={handleClick}>
            {t("* Explore the Hottest New Brands ‚Äì")} <b>{t("Click Here!")}</b>{" "}
            {t("* Explore the Hottest New Brands ‚Äì")} <b>{t("Click Here!")}</b>{" "}
            {t("* Explore the Hottest New Brands ‚Äì")} <b>{t("Click Here!")}</b>{" "}
            {t("* Explore the Hottest New Brands ‚Äì")} <b>{t("Click Here!")}</b>{" "}
          </span>
        </div>
      </div>
    </div>
  );
};

export default Marquee;
